import React, { useState, useEffect, useRef, useCallback } from 'react';
import { X, Download, Database, FileText, Loader, PieChart } from 'lucide-react';
import { database } from '../lib/supabase';
import { downloadCSV } from '../lib/utils';
import toast from 'react-hot-toast';
import Chart from 'chart.js/auto';

const DownloadResearch = ({ onClose }) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [downloading, setDownloading] = useState(false);
  const [stats, setStats] = useState({
    total: 0,
    recent: 0,
    maturidadeData: []
  });
  const chartRef = useRef(null);
  const chartInstance = useRef(null);

  const createChart = useCallback(() => {
    if (chartInstance.current) {
      chartInstance.current.destroy();
    }

    if (chartRef.current && stats.maturidadeData.length > 0) {
      const ctx = chartRef.current.getContext('2d');
      
      const colors = [
        '#0ea5e9', // sky-500
        '#10b981', // emerald-500
        '#f59e0b', // amber-500
        '#ef4444', // red-500
        '#8b5cf6', // violet-500
        '#06b6d4', // cyan-500
        '#84cc16', // lime-500
        '#f97316'  // orange-500
      ];

      chartInstance.current = new Chart(ctx, {
        type: 'doughnut',
        data: {
          labels: stats.maturidadeData.map(item => item.label),
          datasets: [{
            data: stats.maturidadeData.map(item => item.count),
            backgroundColor: colors.slice(0, stats.maturidadeData.length),
            borderColor: '#1e293b',
            borderWidth: 2,
            hoverOffset: 4
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: {
              position: 'bottom',
              labels: {
                color: '#e2e8f0',
                font: {
                  family: 'Inter, sans-serif',
                  size: 12
                },
                padding: 20,
                usePointStyle: true,
                pointStyle: 'circle'
              }
            },
            tooltip: {
              backgroundColor: '#1e293b',
              titleColor: '#e2e8f0',
              bodyColor: '#e2e8f0',
              borderColor: '#334155',
              borderWidth: 1,
              callbacks: {
                label: function(context) {
                  const item = stats.maturidadeData[context.dataIndex];
                  return `${item.label}: ${item.count} (${item.percentage}%)`;
                }
              }
            }
          },
          cutout: '60%'
        }
      });
    }
  }, [stats.maturidadeData]);

  useEffect(() => {
    loadData();
  }, []);

  useEffect(() => {
    if (stats.maturidadeData.length > 0) {
      createChart();
    }
  }, [createChart, stats.maturidadeData.length]);

  useEffect(() => {
    return () => {
      if (chartInstance.current) {
        chartInstance.current.destroy();
      }
    };
  }, []);

  const loadData = async () => {
    setLoading(true);
    try {
      const result = await database.getResearchData();
      
      if (result.success) {
        setData(result.data || []);
        
        // Calcular estatsticas
        const total = result.data?.length || 0;
        const recent = result.data?.filter(item => {
          if (!item.created_at) return false;
          const createdAt = new Date(item.created_at);
          const oneWeekAgo = new Date();
          oneWeekAgo.setDate(oneWeekAgo.getDate() - 7);
          return createdAt > oneWeekAgo;
        }).length || 0;
        
        // Calcular dados de maturidade para o grfico
        const maturidadeCounts = {};
        result.data?.forEach(item => {
          const maturidade = item.maturidade || 'No informado';
          maturidadeCounts[maturidade] = (maturidadeCounts[maturidade] || 0) + 1;
        });
        
        const maturidadeData = Object.entries(maturidadeCounts).map(([label, count]) => ({
          label,
          count,
          percentage: ((count / total) * 100).toFixed(1)
        }));
        
        setStats({ total, recent, maturidadeData });
      } else {
        toast.error('Erro ao carregar dados: ' + result.error);
      }
    } catch (error) {
      console.error('Erro:', error);
      toast.error('Erro inesperado ao carregar dados');
    } finally {
      setLoading(false);
    }
  };

  const handleDownload = async () => {
    if (data.length === 0) {
      toast.error('Nenhum dado disponvel para download');
      return;
    }

    setDownloading(true);
    try {
      // Preparar dados para CSV
      const csvData = data.map(item => ({
        'ID': item.id || '',
        'Nome': item.nome || '',
        'Maturidade': item.maturidade || '',
        'Pontuao': item.pontuacao || '',
        'Respostas': item.respostas || '',
        'Email': item.email || '',
        'Telefone': item.telefone || '',
        'Data de Criao': item.created_at ? new Date(item.created_at).toLocaleDateString('pt-BR') : '-'
      }));

      // Gerar nome do arquivo com timestamp
      const timestamp = new Date().toISOString().split('T')[0];
      const filename = `dados_pesquisa_${timestamp}.csv`;
      
      downloadCSV(csvData, filename);
      toast.success('Download iniciado com sucesso! ');
    } catch (error) {
      console.error('Erro no download:', error);
      toast.error('Erro ao gerar arquivo CSV');
    } finally {
      setDownloading(false);
    }
  };

  const formatDate = (dateString) => {
    if (!dateString) return '-';
    try {
      return new Date(dateString).toLocaleDateString('pt-BR', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    } catch {
      return '-';
    }
  };

  return (
    <div className="p-6">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-3">
          <Database className="w-6 h-6 text-primary-500" />
          <h2 className="text-2xl font-bebas text-white">Dados da Pesquisa</h2>
        </div>
        <button
          onClick={onClose}
          className="text-dark-400 hover:text-white transition-colors"
        >
          <X className="w-6 h-6" />
        </button>
      </div>

      {/* Estatsticas */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
        <div className="card text-center">
          <div className="text-2xl font-bebas text-primary-500 mb-1">{stats.total}</div>
          <div className="text-dark-300 font-mulish text-sm">Total de Registros</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bebas text-primary-500 mb-1">{stats.recent}</div>
          <div className="text-dark-300 font-mulish text-sm">ltimos 7 dias</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bebas text-primary-500 mb-1">{stats.maturidadeData.length}</div>
          <div className="text-dark-300 font-mulish text-sm">Nveis de Maturidade</div>
        </div>
      </div>

      {/* Grfico de Maturidade */}
      {stats.maturidadeData.length > 0 && (
        <div className="mb-6">
          <div className="flex items-center space-x-3 mb-4">
            <PieChart className="w-6 h-6 text-primary-500" />
            <h3 className="text-lg font-bebas text-white">Distribuio por Maturidade</h3>
          </div>
          <div className="card">
            <div className="relative h-80">
              <canvas ref={chartRef}></canvas>
            </div>
          </div>
        </div>
      )}

      {/* Lista de dados */}
      <div className="mb-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-bebas text-white">Registros Recentes</h3>
          <button
            onClick={loadData}
            disabled={loading}
            className="text-primary-500 hover:text-primary-400 transition-colors disabled:opacity-50"
          >
            {loading ? (
              <Loader className="w-5 h-5 animate-spin" />
            ) : (
              'Atualizar'
            )}
          </button>
        </div>

        {loading ? (
          <div className="text-center py-8">
            <Loader className="w-8 h-8 animate-spin text-primary-500 mx-auto mb-4" />
            <p className="text-dark-300 font-mulish">Carregando dados...</p>
          </div>
        ) : data.length === 0 ? (
          <div className="text-center py-8">
            <FileText className="w-12 h-12 text-dark-600 mx-auto mb-4" />
            <p className="text-dark-400 font-mulish">Nenhum dado de pesquisa encontrado</p>
          </div>
        ) : (
          <div className="bg-dark-700/50 rounded-lg overflow-hidden">
            <div className="max-h-64 overflow-y-auto">
              <table className="w-full">
                <thead className="bg-dark-800/50">
                  <tr>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      ID
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Nome
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Maturidade
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Pontuao
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Email
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Telefone
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Data
                    </th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-dark-600">
                  {data.slice(0, 10).map((item, index) => (
                    <tr key={index} className="hover:bg-dark-600/50">
                      <td className="px-4 py-3 text-sm text-white font-mulish">
                        {item.id || '-'}
                      </td>
                      <td className="px-4 py-3 text-sm text-white font-mulish">
                        {item.nome || '-'}
                      </td>
                      <td className="px-4 py-3 text-sm text-primary-400 font-mulish">
                        {item.maturidade || '-'}
                      </td>
                      <td className="px-4 py-3 text-sm text-yellow-400 font-mulish">
                        {item.pontuacao || '-'}
                      </td>
                      <td className="px-4 py-3 text-sm text-dark-300 font-mulish">
                        {item.email || '-'}
                      </td>
                      <td className="px-4 py-3 text-sm text-dark-300 font-mulish">
                        {item.telefone || '-'}
                      </td>
                      <td className="px-4 py-3 text-sm text-dark-400 font-mulish">
                        {formatDate(item.created_at)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            {data.length > 10 && (
              <div className="px-4 py-2 bg-dark-800/50 text-center">
                <p className="text-xs text-dark-400">
                  Mostrando 10 de {data.length} registros
                </p>
              </div>
            )}
          </div>
        )}
      </div>

      {/* Botes de ao */}
      <div className="flex flex-col sm:flex-row gap-4 pt-6 border-t border-dark-700">
        <button
          onClick={onClose}
          className="btn-secondary flex-1"
        >
          Fechar
        </button>
        <button
          onClick={handleDownload}
          disabled={loading || downloading || data.length === 0}
          className="btn-primary flex-1 flex items-center justify-center space-x-2 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {downloading ? (
            <>
              <Loader className="w-5 h-5 animate-spin" />
              <span>Gerando arquivo...</span>
            </>
          ) : (
            <>
              <Download className="w-5 h-5" />
              <span>Baixar CSV ({data.length} registros)</span>
            </>
          )}
        </button>
      </div>

      {/* Informaes adicionais */}
      <div className="mt-6 p-4 bg-dark-700/50 rounded-lg">
        <div className="flex items-start space-x-3">
          <FileText className="w-5 h-5 text-primary-500 mt-0.5 flex-shrink-0" />
          <div className="text-sm text-dark-300">
            <p className="font-medium text-white mb-1">Formato do Arquivo</p>
            <p>O arquivo ser baixado em formato CSV com todos os dados de pesquisa, incluindo informaes de origem e timestamps.</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DownloadResearch;
