import React, { useState, useEffect } from 'react';
import { X, Download, Database, FileText, Loader, BarChart3 } from 'lucide-react';
import { database } from '../lib/supabase';
import { downloadCSV } from '../lib/utils';
import toast from 'react-hot-toast';

const DownloadMain = ({ onClose }) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [downloading, setDownloading] = useState(false);
  const [stats, setStats] = useState({
    total: 0,
    recent: 0,
    sources: [],
    duplicates: 0
  });

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true);
    try {
      const result = await database.getAllLeads();
      
      if (result.success) {
        setData(result.data || []);
        
        // Calcular estatsticas
        const total = result.data?.length || 0;
        const recent = result.data?.filter(item => {
          const createdAt = new Date(item.created_at);
          const oneWeekAgo = new Date();
          oneWeekAgo.setDate(oneWeekAgo.getDate() - 7);
          return createdAt > oneWeekAgo;
        }).length || 0;
        
        const sources = [...new Set(result.data?.map(item => item.origem).filter(Boolean))] || [];
        
        // Calcular duplicatas (mesmo email ou telefone)
        const emails = result.data?.map(item => item.email).filter(Boolean) || [];
        const phones = result.data?.map(item => item.telefone).filter(Boolean) || [];
        const duplicateEmails = emails.length - new Set(emails).size;
        const duplicatePhones = phones.length - new Set(phones).size;
        const duplicates = duplicateEmails + duplicatePhones;
        
        setStats({ total, recent, sources, duplicates });
      } else {
        toast.error('Erro ao carregar dados: ' + result.error);
      }
    } catch (error) {
      console.error('Erro:', error);
      toast.error('Erro inesperado ao carregar dados');
    } finally {
      setLoading(false);
    }
  };

  const handleDownload = async () => {
    if (data.length === 0) {
      toast.error('Nenhum dado disponvel para download');
      return;
    }

    setDownloading(true);
    try {
      // Preparar dados para CSV
      const csvData = data.map(item => ({
        'ID': item.id || '',
        'Nome': item.nome || '',
        'Empresa': item.empresa || '',
        'Email': item.email || '',
        'Telefone': item.telefone || '',
        'Origem': item.origem || '',
        'Data de Criao': new Date(item.created_at).toLocaleDateString('pt-BR'),
        'ltima Atualizao': new Date(item.updated_at || item.created_at).toLocaleDateString('pt-BR'),
        'Status': item.status || 'Ativo'
      }));

      // Gerar nome do arquivo com timestamp
      const timestamp = new Date().toISOString().split('T')[0];
      const filename = `planilha_principal_${timestamp}.csv`;
      
      downloadCSV(csvData, filename);
      toast.success('Download da planilha principal iniciado! ');
    } catch (error) {
      console.error('Erro no download:', error);
      toast.error('Erro ao gerar arquivo CSV');
    } finally {
      setDownloading(false);
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getSourceColor = (source) => {
    const colors = {
      'Manual': 'text-blue-400',
      'CSV Upload': 'text-green-400',
      'RD Station': 'text-purple-400',
      'Apollo': 'text-orange-400',
      'Outros': 'text-gray-400'
    };
    return colors[source] || colors['Outros'];
  };

  return (
    <div className="p-6">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-3">
          <Database className="w-6 h-6 text-primary-500" />
          <h2 className="text-2xl font-bebas text-white">Tabela Principal</h2>
        </div>
        <button
          onClick={onClose}
          className="text-dark-400 hover:text-white transition-colors"
        >
          <X className="w-6 h-6" />
        </button>
      </div>

      {/* Estatsticas */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
        <div className="card text-center">
          <div className="text-2xl font-bebas text-primary-500 mb-1">{stats.total}</div>
          <div className="text-dark-300 font-mulish text-sm">Total de Leads</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bebas text-primary-500 mb-1">{stats.recent}</div>
          <div className="text-dark-300 font-mulish text-sm">ltimos 7 dias</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bebas text-primary-500 mb-1">{stats.sources.length}</div>
          <div className="text-dark-300 font-mulish text-sm">Fontes Diferentes</div>
        </div>
        <div className="card text-center">
          <div className="text-2xl font-bebas text-red-400 mb-1">{stats.duplicates}</div>
          <div className="text-dark-300 font-mulish text-sm">Possveis Duplicatas</div>
        </div>
      </div>

      {/* Anlise por fonte */}
      {stats.sources.length > 0 && (
        <div className="mb-6">
          <h3 className="text-lg font-bebas text-white mb-3 flex items-center">
            <BarChart3 className="w-5 h-5 mr-2" />
            Distribuio por Fonte
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {stats.sources.map((source, index) => {
              const count = data.filter(item => item.origem === source).length;
              const percentage = ((count / stats.total) * 100).toFixed(1);
              return (
                <div key={index} className="card">
                  <div className="flex items-center justify-between mb-2">
                    <span className={`font-medium ${getSourceColor(source)}`}>
                      {source}
                    </span>
                    <span className="text-dark-400 text-sm">{percentage}%</span>
                  </div>
                  <div className="w-full bg-dark-700 rounded-full h-2">
                    <div
                      className="bg-primary-500 h-2 rounded-full transition-all duration-500"
                      style={{ width: `${percentage}%` }}
                    ></div>
                  </div>
                  <div className="text-sm text-dark-300 mt-1">{count} registros</div>
                </div>
              );
            })}
          </div>
        </div>
      )}

      {/* Lista de dados recentes */}
      <div className="mb-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-bebas text-white">Leads Recentes</h3>
          <button
            onClick={loadData}
            disabled={loading}
            className="text-primary-500 hover:text-primary-400 transition-colors disabled:opacity-50"
          >
            {loading ? (
              <Loader className="w-5 h-5 animate-spin" />
            ) : (
              'Atualizar'
            )}
          </button>
        </div>

        {loading ? (
          <div className="text-center py-8">
            <Loader className="w-8 h-8 animate-spin text-primary-500 mx-auto mb-4" />
            <p className="text-dark-300 font-mulish">Carregando dados...</p>
          </div>
        ) : data.length === 0 ? (
          <div className="text-center py-8">
            <FileText className="w-12 h-12 text-dark-600 mx-auto mb-4" />
            <p className="text-dark-400 font-mulish">Nenhum lead encontrado</p>
            <p className="text-dark-500 text-sm mt-2">
              Adicione dados manualmente ou faa upload de um arquivo CSV
            </p>
          </div>
        ) : (
          <div className="bg-dark-700/50 rounded-lg overflow-hidden">
            <div className="max-h-64 overflow-y-auto">
              <table className="w-full">
                <thead className="bg-dark-800/50">
                  <tr>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Nome
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Empresa
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Origem
                    </th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-dark-300 uppercase tracking-wider">
                      Data
                    </th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-dark-600">
                  {data.slice(0, 10).map((item, index) => (
                    <tr key={index} className="hover:bg-dark-600/50">
                      <td className="px-4 py-3 text-sm text-white font-mulish">
                        {item.nome || '-'}
                      </td>
                      <td className="px-4 py-3 text-sm text-dark-300 font-mulish">
                        {item.empresa || '-'}
                      </td>
                      <td className="px-4 py-3 text-sm">
                        <span className={`${getSourceColor(item.origem)} font-medium`}>
                          {item.origem || '-'}
                        </span>
                      </td>
                      <td className="px-4 py-3 text-sm text-dark-400 font-mulish">
                        {formatDate(item.created_at)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            {data.length > 10 && (
              <div className="px-4 py-2 bg-dark-800/50 text-center">
                <p className="text-xs text-dark-400">
                  Mostrando 10 de {data.length} registros
                </p>
              </div>
            )}
          </div>
        )}
      </div>

      {/* Botes de ao */}
      <div className="flex flex-col sm:flex-row gap-4 pt-6 border-t border-dark-700">
        <button
          onClick={onClose}
          className="btn-secondary flex-1"
        >
          Fechar
        </button>
        <button
          onClick={handleDownload}
          disabled={loading || downloading || data.length === 0}
          className="btn-primary flex-1 flex items-center justify-center space-x-2 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {downloading ? (
            <>
              <Loader className="w-5 h-5 animate-spin" />
              <span>Gerando arquivo...</span>
            </>
          ) : (
            <>
              <Download className="w-5 h-5" />
              <span>Baixar Planilha Principal ({data.length} leads)</span>
            </>
          )}
        </button>
      </div>

      {/* Informaes adicionais */}
      <div className="mt-6 p-4 bg-dark-700/50 rounded-lg">
        <div className="flex items-start space-x-3">
          <FileText className="w-5 h-5 text-primary-500 mt-0.5 flex-shrink-0" />
          <div className="text-sm text-dark-300">
            <p className="font-medium text-white mb-1">Planilha Completa</p>
            <p>O arquivo incluir todos os leads da planilha principal com informaes detalhadas de origem, timestamps e status.</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DownloadMain;
