import React, { useState } from 'react';
import { X, User, Building, Mail, Phone, CheckCircle } from 'lucide-react';
import { database } from '../lib/supabase';
import { validateEmail, sanitizeInput, formatBrazilInternationalPhone } from '../lib/utils';
import toast from 'react-hot-toast';

const ManualForm = ({ onClose }) => {
  const [formData, setFormData] = useState({
    nome: '',
    empresa: '',
    email: '',
    telefone: '',
    msg_inicial: false,
    questionario_completo: false,
    reuniao_criada: false,
    full_time: false
  });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});


  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    
    // Mapear os novos nomes de campos para os nomes originais
    const fieldMapping = {
      'fullname': 'nome',
      'company': 'empresa',
      'mail': 'email',
      'phone': 'telefone',
      'full_time': 'full_time'
    };
    
    const fieldName = fieldMapping[name] || name;
    let processedValue = value;

    if (fieldName === 'telefone') {
      // Apenas nmeros, mximo 20 dgitos
      const numbers = value.replace(/\D/g, '');
      if (numbers.length <= 20) {
        processedValue = numbers;
      } else {
        return; // No permitir mais de 20 dgitos
      }
    } else if (fieldName === 'nome') {
      // Remover apenas caracteres proibidos, manter espaos; limitar a 200
      processedValue = value.replace(/[<>]/g, '').slice(0, 200);
    } else if (fieldName === 'empresa') {
      // Remover apenas caracteres proibidos, manter espaos; limitar a 100
      processedValue = value.replace(/[<>]/g, '').slice(0, 100);
    } else {
      // Demais campos: sanitizar sem bloquear espaos internos
      processedValue = sanitizeInput(value);
    }

    setFormData(prev => ({
      ...prev,
      [fieldName]: type === 'checkbox' ? checked : processedValue
    }));

    // Limpar erro do campo quando usurio comear a digitar
    if (errors[fieldName]) {
      setErrors(prev => ({
        ...prev,
        [fieldName]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};

    if (!formData.nome.trim()) {
      newErrors.nome = 'Nome  obrigatrio';
    } else if (formData.nome.trim().length < 2) {
      newErrors.nome = 'Nome deve ter pelo menos 2 caracteres';
    }

    if (!formData.empresa.trim()) {
      newErrors.empresa = 'Nome da empresa  obrigatrio';
    }

    if (formData.email.trim() && !validateEmail(formData.email)) {
      newErrors.email = 'Email invlido';
    }

    // Telefone  opcional, s valida se estiver preenchido
    if (formData.telefone && formData.telefone.length > 0) {
      const phoneDigits = formData.telefone.replace(/\D/g, '');
      if (phoneDigits.length < 10) {
        newErrors.telefone = 'Telefone deve ter pelo menos 10 dgitos';
      }
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      toast.error('Por favor, corrija os erros no formulrio');
      return;
    }

    setLoading(true);

    try {
      // Verificar duplicatas no Supabase usando apenas dgitos do telefone
      const phoneDigitsForCheck = formData.telefone ? formData.telefone.replace(/\D/g, '') : '';
      const duplicateCheck = await database.checkDuplicate(formData.email, phoneDigitsForCheck);
      
      if (duplicateCheck.exists) {
        toast.error('Este email ou telefone j existe no sistema');
        setLoading(false);
        return;
      }

      // Processar telefone: enviar somente dgitos
      const phoneDigits = formData.telefone ? formData.telefone.replace(/\D/g, '') : '';
      
      // Inserir dados no Supabase
      const result = await database.insertManualData({
        nome: formData.nome.trim(),
        empresa: formData.empresa.trim(),
        email: formData.email.trim() ? formData.email.trim().toLowerCase() : 'EMPTY',
        telefone: phoneDigits,
        msg_inicial: !!formData.msg_inicial,
        questionario_completo: !!formData.questionario_completo,
        reuniao_criada: !!formData.reuniao_criada,
        full_time: !!formData.full_time
      });

      if (result.success) {
        toast.success('Dados inseridos com sucesso no Supabase! ');
        setFormData({ 
          nome: '', 
          empresa: '', 
          email: '', 
          telefone: '',
          msg_inicial: false,
          questionario_completo: false,
          reuniao_criada: false,
          full_time: false
        });
        setErrors({});
      } else {
        toast.error(result.error || 'Erro ao inserir dados no Supabase');
      }
      
    } catch (error) {
      console.error('Erro:', error);
      toast.error('Erro de conexo com Supabase. Verifique sua configurao.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-6">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-3">
          <User className="w-6 h-6 text-primary-500" />
          <h2 className="text-2xl font-bebas text-white">Inserir Dados Manualmente</h2>
        </div>
        <button
          onClick={onClose}
          className="text-dark-400 hover:text-white transition-colors"
        >
          <X className="w-6 h-6" />
        </button>
      </div>


      {/* Formulrio */}
      <form onSubmit={handleSubmit} className="space-y-6 no-autocomplete" autoComplete="off" autocorrect="off" spellcheck="false" data-lpignore="true">
        {/* Campos falsos para enganar o navegador */}
        <input type="text" name="fakeuser" autoComplete="off" style={{ display: 'none' }} />
        <input type="password" name="fakepassword" autoComplete="off" style={{ display: 'none' }} />
        <input type="email" name="fakeemail" autoComplete="off" style={{ display: 'none' }} />
        <input type="tel" name="fakephone" autoComplete="off" style={{ display: 'none' }} />
        <input type="text" name="fakecompany" autoComplete="off" style={{ display: 'none' }} />

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {/* Nome */}
          <div>
            <label className="block text-sm font-medium text-dark-200 mb-2">
              Nome Completo *
            </label>
            <div className="relative">
              <User className="absolute left-3 top-1/2 transform -translate-y-1/2 text-dark-400 w-5 h-5" />
              <input
                type="text"
                name="fullname"
                value={formData.nome}
                onChange={handleChange}
                className={`input-field pl-10 ${errors.nome ? 'border-red-500' : ''}`}
                placeholder="Digite o nome completo"
                autoComplete="new-password"
                autocorrect="off"
                spellCheck="false"
                maxLength={200}
                data-lpignore="true"
                data-form-type="other"
              />
            </div>
            {errors.nome && (
              <p className="text-red-400 text-sm mt-1">{errors.nome}</p>
            )}
          </div>

          {/* Empresa */}
          <div>
            <label className="block text-sm font-medium text-dark-200 mb-2">
              Nome da Empresa *
            </label>
            <div className="relative">
              <Building className="absolute left-3 top-1/2 transform -translate-y-1/2 text-dark-400 w-5 h-5" />
              <input
                type="text"
                name="company"
                value={formData.empresa}
                onChange={handleChange}
                className={`input-field pl-10 ${errors.empresa ? 'border-red-500' : ''}`}
                placeholder="Digite o nome da empresa"
                autoComplete="new-password"
                autocorrect="off"
                spellCheck="false"
                maxLength={100}
                data-lpignore="true"
                data-form-type="other"
              />
            </div>
            {errors.empresa && (
              <p className="text-red-400 text-sm mt-1">{errors.empresa}</p>
            )}
          </div>

          {/* Email */}
          <div>
            <label className="block text-sm font-medium text-dark-200 mb-2">
              Email *
            </label>
            <div className="relative">
              <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 text-dark-400 w-5 h-5" />
              <input
                type="email"
                name="mail"
                value={formData.email}
                onChange={handleChange}
                className={`input-field pl-10 ${errors.email ? 'border-red-500' : ''}`}
                placeholder="exemplo@empresa.com"
                autoComplete="new-password"
                autocorrect="off"
                spellCheck="false"
                data-lpignore="true"
                data-form-type="other"
              />
            </div>
            {errors.email && (
              <p className="text-red-400 text-sm mt-1">{errors.email}</p>
            )}
          </div>

          {/* Telefone */}
          <div>
            <label className="block text-sm font-medium text-dark-200 mb-2">
              Telefone
            </label>
            <div className="relative">
              <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 text-dark-400 w-5 h-5" />
              <input
                type="tel"
                name="phone"
                value={formatBrazilInternationalPhone(formData.telefone)}
                onChange={handleChange}
                className={`input-field pl-10 ${errors.telefone ? 'border-red-500' : ''}`}
                placeholder="+55 85 9XXXX-XXXX"
                maxLength="20"
                autoComplete="new-password"
                autocorrect="off"
                spellCheck="false"
                data-lpignore="true"
                data-form-type="other"
              />
            </div>
            {errors.telefone && (
              <p className="text-red-400 text-sm mt-1">{errors.telefone}</p>
            )}
            <p className="text-dark-400 text-xs mt-1">
              Digite o cdigo do pas + nmero (ex: 5511999999999)
            </p>
          </div>
        </div>

        {/* Flags de processo */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          <div>
            <label className="inline-flex items-center space-x-2">
              <input
                type="checkbox"
                name="msg_inicial"
                checked={formData.msg_inicial}
                onChange={handleChange}
                className="form-checkbox h-4 w-4 text-primary-500"
              />
              <span className="text-sm text-dark-200">Mensagem inicial j enviada?</span>
            </label>
            <p className="text-dark-400 text-xs mt-1">
              msg_inicial: false = precisa enviar; true = j enviada
            </p>
          </div>
          <div>
            <label className="inline-flex items-center space-x-2">
              <input
                type="checkbox"
                name="questionario_completo"
                checked={formData.questionario_completo}
                onChange={handleChange}
                className="form-checkbox h-4 w-4 text-primary-500"
              />
              <span className="text-sm text-dark-200">Questionrio concludo?</span>
            </label>
            <p className="text-dark-400 text-xs mt-1">
              questionario_completo: true = vai para agendamento
            </p>
          </div>
          <div>
            <label className="inline-flex items-center space-x-2">
              <input
                type="checkbox"
                name="reuniao_criada"
                checked={formData.reuniao_criada}
                onChange={handleChange}
                className="form-checkbox h-4 w-4 text-primary-500"
              />
              <span className="text-sm text-dark-200">Reunio criada?</span>
            </label>
            <p className="text-dark-400 text-xs mt-1">
              reuniao_criada: true = reunio j marcada
            </p>
          </div>
          <div>
            <label className="inline-flex items-center space-x-2">
              <input
                type="checkbox"
                name="full_time"
                checked={formData.full_time}
                onChange={handleChange}
                className="form-checkbox h-4 w-4 text-primary-500"
              />
              <span className="text-sm text-dark-200">Full time (enviar fora do horrio comercial)</span>
            </label>
            <p className="text-dark-400 text-xs mt-1">
              Quando marcado, o envio ocorrer fora do horrio comercial.
            </p>
          </div>
        </div>

        {/* Botes */}
        <div className="flex flex-col sm:flex-row gap-4 pt-6 border-t border-dark-700">
          <button
            type="button"
            onClick={onClose}
            className="btn-secondary flex-1"
          >
            Cancelar
          </button>
          <button
            type="submit"
            disabled={loading}
            className="btn-primary flex-1 flex items-center justify-center space-x-2 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {loading ? (
              <>
                <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                <span>Inserindo...</span>
              </>
            ) : (
              <>
                <CheckCircle className="w-5 h-5" />
                <span>Enviar Dados</span>
              </>
            )}
          </button>
        </div>
      </form>

      {/* Informaes adicionais */}
      <div className="mt-6 p-4 bg-dark-700/50 rounded-lg">
        <div className="flex items-start space-x-3">
          <CheckCircle className="w-5 h-5 text-primary-500 mt-0.5 flex-shrink-0" />
          <div className="text-sm text-dark-300">
            <p className="font-medium text-white mb-1">Validao Automtica</p>
            <p>Os dados sero validados automaticamente e duplicatas sero detectadas antes da insero no banco de dados.</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManualForm;
