import React, { useState, useRef, useMemo } from 'react';
import { X, Upload, FileText, CheckCircle, AlertCircle, Eye, EyeOff, AlertTriangle } from 'lucide-react';
import { database } from '../lib/supabase';
import { processCSVFile, processXLSXFile } from '../lib/utils';
import toast from 'react-hot-toast';

const UploadCSV = ({ onClose }) => {
  const [file, setFile] = useState(null);
  const [previewData, setPreviewData] = useState([]);
  const [columnMap, setColumnMap] = useState({ nome: '', empresa: '', email: '', telefone: '', origem: '' });
  const [loading, setLoading] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [showPreview, setShowPreview] = useState(false);
  const [showConfirmation, setShowConfirmation] = useState(false);
  const [confirmationData, setConfirmationData] = useState(null);
  const [errors, setErrors] = useState([]);
  const [isDragOver, setIsDragOver] = useState(false);
  const fileInputRef = useRef(null);
  const [manualOrigem, setManualOrigem] = useState('');
  const [addBrazilPrefix, setAddBrazilPrefix] = useState(false);
  const [fullTimeUpload, setFullTimeUpload] = useState(false);

  // Preview derivado com base no mapeamento atual
  const mappedPreview = useMemo(() => {
    if (!previewData || previewData.length === 0) return [];
    const mapField = (key) => columnMap[key];
    
    // Funo para tratar telefone vazio no preview
    const tratarTelefonePreview = (telefone) => {
      const limpo = String(telefone).replace(/\D/g, '');
      if (!limpo) return 'EMPTY';
      // Limitar a 50 caracteres para evitar erro no banco
      return limpo.length > 50 ? limpo.substring(0, 50) : limpo;
    };
    
    return previewData.slice(0, 10).map(row => {
      const telefoneRaw = mapField('telefone') ? String(row[mapField('telefone')] || '').replace(/\D/g, '') : '';
      return {
        nome: mapField('nome') ? (row[mapField('nome')] || '') : '',
        empresa: mapField('empresa') ? (row[mapField('empresa')] || '') : '',
        email: mapField('email') ? (row[mapField('email')] || 'EMPTY') : 'EMPTY',
        telefone: tratarTelefonePreview(telefoneRaw),
        origem: manualOrigem ? manualOrigem : (mapField('origem') ? (row[mapField('origem')] || '') : 'CSV Upload'),
        msg_inicial: false,
        questionario_completo: false,
        reuniao_criada: false
      };
    });
  }, [previewData, columnMap, manualOrigem]);

  const processFile = async (selectedFile) => {
    // Validar tipo de arquivo (CSV ou XLSX)
    const lower = selectedFile.name.toLowerCase();
    const isCSV = lower.endsWith('.csv');
    const isXLSX = lower.endsWith('.xlsx');
    if (!isCSV && !isXLSX) {
      toast.error('Selecione um arquivo .csv ou .xlsx vlido');
      return;
    }

    // Validar tamanho (mximo 100MB)
    if (selectedFile.size > 100 * 1024 * 1024) {
      toast.error('Arquivo muito grande. Tamanho mximo: 100MB');
      return;
    }

    setFile(selectedFile);
    setErrors([]);
    setPreviewData([]);
    setColumnMap({ nome: '', empresa: '', email: '', telefone: '', origem: '' });
    setShowPreview(false);
    setManualOrigem('');
    setAddBrazilPrefix(false);
    setFullTimeUpload(false);

    try {
      setLoading(true);
      const data = isCSV ? await processCSVFile(selectedFile) : await processXLSXFile(selectedFile);
      
      if (data.length === 0) {
        toast.error('Arquivo CSV est vazio ou invlido');
        return;
      }

      setPreviewData(data);
      
      // Auto-mapear pelos nomes das colunas
      const columns = Object.keys(data[0]);
      const lowerColumns = columns.map(c => c.toLowerCase().trim());
      
      const findCol = (patterns) => {
        for (const pattern of patterns) {
          const idx = lowerColumns.findIndex(c => c.includes(pattern.toLowerCase()));
          if (idx >= 0) return columns[idx];
        }
        return '';
      };
      
      setColumnMap({
        nome: findCol(['nome', 'name', 'cliente', 'pessoa', 'contato']),
        empresa: findCol(['empresa', 'company', 'organizao', 'organizacao', 'cliente']),
        email: findCol(['email', 'e-mail', 'mail', 'correio']),
        telefone: findCol(['telefone', 'phone', 'tel', 'celular', 'cel', 'whatsapp', 'fone']),
        origem: findCol(['origem', 'source', 'fonte', 'canal', 'lead'])
      });
      
      toast.success(`Arquivo carregado com sucesso! ${data.length} registros encontrados.`);
    } catch (error) {
      console.error('Erro ao processar arquivo:', error);
      toast.error('Erro ao processar arquivo: ' + error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleFileSelect = async (e) => {
    const selectedFile = e.target.files[0];
    if (!selectedFile) return;
    await processFile(selectedFile);
  };

  const handleDragOver = (e) => {
    e.preventDefault();
    setIsDragOver(true);
  };

  const handleDragLeave = (e) => {
    e.preventDefault();
    setIsDragOver(false);
  };

  const handleDrop = async (e) => {
    e.preventDefault();
    setIsDragOver(false);
    
    const files = e.dataTransfer.files;
    if (files.length > 0) {
      await processFile(files[0]);
    }
  };

  // validateData removido (no utilizado)


  const handleUpload = async () => {
    if (!file || previewData.length === 0) {
      toast.error('Nenhum arquivo selecionado');
      return;
    }

    // Checar mapeamentos obrigatrios (email  opcional)
    const required = ['nome','empresa','telefone'];
    const missing = required.filter(key => !columnMap[key]);
    if (missing.length > 0) {
      toast.error(`Mapeie as colunas: ${missing.join(', ')}`);
      return;
    }
    if (!manualOrigem || !manualOrigem.trim()) {
      toast.error('Informe a origem manual para esta importao');
      return;
    }

    // Gerar dados mapeados para a tabela origin_dados_lead
    const mapField = (target) => columnMap[target];
    const initialData = previewData.map(row => {
      const telefoneRaw = String(row[mapField('telefone')] || '').replace(/\D/g, '');
      return {
        nome: row[mapField('nome')] || '',
        empresa: row[mapField('empresa')] || '',
        email: row[mapField('email')] || 'EMPTY',
        telefone: telefoneRaw,
        origem: manualOrigem || 'CSV Upload',
        full_time: !!fullTimeUpload,
        msg_inicial: false,
        questionario_completo: false,
        reuniao_criada: false
      };
    });

    // Funo para tratar telefones vazios apenas
    const tratarTelefoneVazio = (telefone) => {
      const limpo = String(telefone).replace(/\D/g, '');
      if (!limpo) return 'EMPTY';
      // Limitar a 50 caracteres para evitar erro no banco
      return limpo.length > 50 ? limpo.substring(0, 50) : limpo;
    };

    // Aplicar tratamento apenas para telefones vazios
    let correctedData = initialData.map(row => ({
      ...row,
      telefone: tratarTelefoneVazio(row.telefone)
    }));

    if (addBrazilPrefix) {
      correctedData = correctedData.map(row => {
        const onlyDigits = String(row.telefone).replace(/\D/g, '');
        if (onlyDigits && !onlyDigits.startsWith('55')) {
          const prefixed = '55' + onlyDigits;
          return { ...row, telefone: prefixed.length > 50 ? prefixed.substring(0, 50) : prefixed };
        }
        return row;
      });
    }

    // Calcular estatsticas
    const stats = {
      total: correctedData.length,
      emailsFaltantes: correctedData.filter(row => row.email === 'EMPTY').length,
      telefonesVazios: initialData.filter(row => !String(row.telefone).replace(/\D/g, '')).length
    };

    // Se houver telefones vazios, mostrar modal de confirmao
    setConfirmationData({ data: correctedData, stats });
    setShowConfirmation(true);
  };

  const enviarDados = async (data) => {
    setUploading(true);
    setShowConfirmation(false);

    try {
      // Inserir dados no banco
      const result = await database.insertCsvData(data, manualOrigem || 'CSV Upload');

      if (result.success) {
        const message = result.duplicates > 0 
          ? `Upload concludo! ${result.inserted} registros inseridos, ${result.duplicates} duplicatas ignoradas.`
          : `Upload concludo! ${result.inserted} registros inseridos com sucesso.`;
        
        toast.success(message);
        
        // Limpar formulrio
        setFile(null);
        setPreviewData([]);
        setShowPreview(false);
        setShowConfirmation(false);
        setConfirmationData(null);
        setErrors([]);
        if (fileInputRef.current) {
          fileInputRef.current.value = '';
        }
        setManualOrigem('');
        setAddBrazilPrefix(false);
        setFullTimeUpload(false);
      } else {
        toast.error(result.error || 'Erro ao fazer upload dos dados');
      }
    } catch (error) {
      console.error('Erro no upload:', error);
      toast.error('Erro inesperado durante o upload');
    } finally {
      setUploading(false);
    }
  };

  const confirmUpload = async () => {
    if (!confirmationData) return;
    await enviarDados(confirmationData.data);
  };

  const formatFileSize = (bytes) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  return (
    <div className="p-6">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-3">
          <Upload className="w-6 h-6 text-primary-500" />
          <h2 className="text-2xl font-bebas text-white">Upload de Planilha CSV</h2>
        </div>
        <button
          onClick={onClose}
          className="text-dark-400 hover:text-white transition-colors"
        >
          <X className="w-6 h-6" />
        </button>
      </div>

      {/* Upload de arquivo */}
      <div className="mb-6">
        <label className="block text-sm font-medium text-dark-200 mb-2">
          Selecionar Arquivo CSV/XLSX
        </label>
        <div className="relative">
          <input
            ref={fileInputRef}
            type="file"
            accept=".csv,.xlsx"
            onChange={handleFileSelect}
            className="hidden"
            disabled={loading}
          />
          <div
            onClick={() => fileInputRef.current?.click()}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
            disabled={loading}
            className={`w-full p-6 border-2 border-dashed rounded-lg transition-all duration-200 cursor-pointer ${
              isDragOver 
                ? 'border-primary-500 bg-primary-500/10' 
                : 'border-dark-600 hover:border-primary-500'
            } disabled:opacity-50 disabled:cursor-not-allowed`}
          >
            <div className="text-center">
              {loading ? (
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-500 mx-auto mb-4"></div>
              ) : (
                <Upload className={`w-8 h-8 mx-auto mb-4 ${isDragOver ? 'text-primary-500' : 'text-primary-500'}`} />
              )}
              <p className="text-white font-mulish mb-2">
                {loading 
                  ? 'Processando arquivo...' 
                  : isDragOver 
                    ? 'Solte o arquivo aqui' 
                    : 'Arraste e solte ou clique para selecionar um arquivo CSV ou XLSX'
                }
              </p>
              <p className="text-dark-400 text-sm">
                Mximo 100MB  Arquivos .csv ou .xlsx
              </p>
            </div>
          </div>
        </div>

        {file && (
          <div className="mt-4 p-4 bg-dark-700/50 rounded-lg">
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-3">
                <FileText className="w-5 h-5 text-primary-500" />
                <div>
                  <p className="text-white font-mulish">{file.name}</p>
                  <p className="text-dark-400 text-sm">{formatFileSize(file.size)}</p>
                </div>
              </div>
              <button
                onClick={() => setShowPreview(!showPreview)}
                className="text-primary-500 hover:text-primary-400 transition-colors"
              >
                {showPreview ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Erros de validao */}
      {errors.length > 0 && (
        <div className="mb-6 p-4 bg-red-500/20 border border-red-500/50 rounded-lg">
          <div className="flex items-start space-x-3">
            <AlertCircle className="w-5 h-5 text-red-400 mt-0.5 flex-shrink-0" />
            <div>
              <h4 className="text-red-400 font-medium mb-2">Erros encontrados no arquivo:</h4>
              <ul className="text-red-300 text-sm space-y-1">
                {errors.slice(0, 5).map((error, index) => (
                  <li key={index}> {error}</li>
                ))}
                {errors.length > 5 && (
                  <li> ... e mais {errors.length - 5} erros</li>
                )}
              </ul>
            </div>
          </div>
        </div>
      )}

      {/* Mapeamento e preview dos dados */}
      {showPreview && previewData.length > 0 && (
        <div className="mb-6">
          <h3 className="text-lg font-bebas text-white mb-4">Mapeamento de Colunas</h3>

          {/* Colunas disponveis */}
          <div className="mb-4 p-4 bg-dark-700/50 rounded-lg">
            <h4 className="text-sm font-medium text-white mb-3">Colunas disponveis na planilha:</h4>
            <div className="flex flex-wrap gap-2">
              {Object.keys(previewData[0]).map((col, idx) => (
                <span key={idx} className="px-2 py-1 bg-dark-600 text-dark-300 text-xs rounded">
                  {col}
                </span>
              ))}
            </div>
          </div>

          {/* Mapeamento minimalista */}
          <div className="mb-4 p-4 bg-dark-700/50 rounded-lg">
            <p className="text-sm text-dark-300 mb-3">Associe as colunas da planilha aos campos do sistema:</p>
            {['nome','empresa','email','telefone'].map((target) => (
              <div key={target} className="grid grid-cols-1 md:grid-cols-3 gap-3 items-center mb-3">
                <label className="text-sm text-white md:text-right md:pr-4 capitalize">
                  {target} {['nome','empresa','telefone'].includes(target) && <span className="text-red-400">*</span>}
                </label>
                <select
                  className="input-field md:col-span-2"
                  value={columnMap[target] || ''}
                  onChange={(e) => {
                    const value = e.target.value;
                    setColumnMap(prev => ({ ...prev, [target]: value }));
                  }}
                >
                  <option value="">Selecione a coluna...</option>
                  {Object.keys(previewData[0]).map((col) => (
                    <option key={col} value={col}>{col}</option>
                  ))}
                </select>
              </div>
            ))}
            <p className="text-xs text-dark-400">Observao: msg_inicial, questionario_completo e reuniao_criada sero definidos como false por padro.</p>
          </div>
          {/* Origem manual */}
          <div className="mb-4 p-4 bg-dark-700/50 rounded-lg">
            <label className="block text-sm font-medium text-white mb-2">Origem desta importao *</label>
            <input
              type="text"
              className="input-field"
              placeholder="Ex: apollo, RD Station"
              value={manualOrigem}
              onChange={(e) => setManualOrigem(e.target.value)}
              maxLength={100}
            />
            <p className="text-xs text-dark-400 mt-1">Este valor ser gravado em todas as linhas como coluna origem.</p>
          </div>
          

          {/* Preview mapeado (como ser inserido) */}
          <div className="bg-dark-700/50 rounded-lg overflow-hidden">
            <div className="px-3 py-2 bg-dark-800/50">
              <h4 className="text-sm font-medium text-white">Prvia dos dados mapeados (primeiras 10 linhas)</h4>
            </div>
            <div className="max-h-64 overflow-y-auto">
              <table className="w-full">
                <thead className="bg-dark-800/50">
                  <tr>
                    {['nome','empresa','email','telefone','origem','msg_inicial','questionario_completo','reuniao_criada'].map(h => (
                      <th key={h} className="px-3 py-2 text-left text-xs font-medium text-dark-300 uppercase">{h}</th>
                    ))}
                  </tr>
                </thead>
                <tbody className="divide-y divide-dark-600">
                  {mappedPreview.map((row, idx) => (
                    <tr key={idx} className="hover:bg-dark-600/50">
                      <td className="px-3 py-2 text-sm text-dark-300 max-w-xs truncate" title={row.nome}>
                        {row.nome || '-'}
                      </td>
                      <td className="px-3 py-2 text-sm text-dark-300 max-w-xs truncate" title={row.empresa}>
                        {row.empresa || '-'}
                      </td>
                      <td className="px-3 py-2 text-sm text-dark-300 max-w-xs truncate" title={row.email}>
                        {row.email || '-'}
                      </td>
                      <td className="px-3 py-2 text-sm text-dark-300 max-w-xs truncate" title={row.telefone}>
                        {row.telefone || '-'}
                      </td>
                      <td className="px-3 py-2 text-sm text-dark-300 max-w-xs truncate" title={row.origem}>
                        {row.origem || '-'}
                      </td>
                      <td className="px-3 py-2 text-sm text-dark-300 text-center">
                        {String(row.msg_inicial)}
                      </td>
                      <td className="px-3 py-2 text-sm text-dark-300 text-center">
                        {String(row.questionario_completo)}
                      </td>
                      <td className="px-3 py-2 text-sm text-dark-300 text-center">
                        {String(row.reuniao_criada)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      )}

      {/* Botes de ao */}
      <div className="flex flex-col sm:flex-row gap-4 pt-6 border-t border-dark-700">
        <button
          onClick={onClose}
          className="btn-secondary flex-1"
        >
          Cancelar
        </button>
        <button
          onClick={handleUpload}
          disabled={!file || previewData.length === 0 || errors.length > 0 || uploading}
          className="btn-primary flex-1 flex items-center justify-center space-x-2 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {uploading ? (
            <>
              <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
              <span>Enviando...</span>
            </>
          ) : (
            <>
              <CheckCircle className="w-5 h-5" />
              <span>Alimentar Planilha ({previewData.length} registros)</span>
            </>
          )}
        </button>
      </div>

      {/* Informaes adicionais */}
      <div className="mt-6 p-4 bg-dark-700/50 rounded-lg">
        <div className="flex items-start space-x-3">
          <CheckCircle className="w-5 h-5 text-primary-500 mt-0.5 flex-shrink-0" />
          <div className="text-sm text-dark-300">
            <p className="font-medium text-white mb-1">Processamento Automtico</p>
            <p>Os dados sero validados automaticamente e duplicatas sero removidas antes da insero no banco de dados.</p>
          </div>
        </div>
      </div>

      {/* Modal de Confirmao */}
      {showConfirmation && confirmationData && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-dark-800 rounded-lg max-w-lg w-full p-6">
            <div className="flex items-center space-x-3 mb-4">
              <div className="w-10 h-10 bg-yellow-500/20 rounded-full flex items-center justify-center">
                <AlertTriangle className="w-5 h-5 text-yellow-500" />
              </div>
              <h3 className="text-lg font-semibold text-white">Correo de Campos Vazios</h3>
            </div>
            
            <div className="space-y-4 mb-6">
              <p className="text-dark-300">
                Detectamos campos vazios que sero definidos como "EMPTY".
              </p>
              
              <div className="bg-dark-700/50 rounded-lg p-4 space-y-3">
                <h4 className="text-sm font-medium text-white mb-3">Resumo das correes:</h4>
                
                <div className="space-y-2">
                  {confirmationData.stats.telefonesVazios > 0 && (
                    <div className="flex items-center space-x-2">
                      <div className="w-2 h-2 bg-orange-500 rounded-full"></div>
                      <span className="text-sm text-dark-300">
                        <span className="text-orange-400 font-semibold">{confirmationData.stats.telefonesVazios}</span> telefones vazios sero definidos como "EMPTY"
                      </span>
                    </div>
                  )}
                  
                  {confirmationData.stats.emailsFaltantes > 0 && (
                    <div className="flex items-center space-x-2">
                      <div className="w-2 h-2 bg-yellow-500 rounded-full"></div>
                      <span className="text-sm text-dark-300">
                        <span className="text-yellow-400 font-semibold">{confirmationData.stats.emailsFaltantes}</span> emails sero definidos como "EMPTY"
                      </span>
                    </div>
                  )}
                </div>
                
                <div className="mt-4 p-3 bg-dark-600/50 rounded border-l-4 border-orange-500">
                  <p className="text-xs text-orange-300">
                    <strong>Exemplo:</strong> Campos vazios sero definidos como "EMPTY"
                  </p>
                </div>
              </div>
              
              <div className="bg-dark-700/50 rounded-lg p-4">
                <label className="inline-flex items-center space-x-3">
                  <input
                    type="checkbox"
                    className="form-checkbox h-4 w-4 text-primary-500"
                    checked={addBrazilPrefix}
                    onChange={(e) => setAddBrazilPrefix(e.target.checked)}
                  />
                  <span className="text-sm text-white">Colocar 55 na frente de todos os nmeros</span>
                </label>
                <p className="text-xs text-dark-400 mt-2">Quando marcado, adicionaremos 55 aos nmeros que no comearem com 55.</p>
              </div>

            <div className="bg-dark-700/50 rounded-lg p-4">
              <label className="inline-flex items-center space-x-3">
                <input
                  type="checkbox"
                  className="form-checkbox h-4 w-4 text-primary-500"
                  checked={fullTimeUpload}
                  onChange={(e) => setFullTimeUpload(e.target.checked)}
                />
                <span className="text-sm text-white">Full time (enviar fora do horrio comercial)</span>
              </label>
              <p className="text-xs text-dark-400 mt-2">Marque para cadastrar todos os registros com full_time = true.</p>
            </div>

              <div className="bg-dark-700/30 rounded-lg p-3">
                <p className="text-sm text-dark-300">
                  Total de registros: <span className="text-white font-semibold">{confirmationData.stats.total}</span>
                </p>
              </div>
            </div>
            
            <div className="flex space-x-3">
              <button
                onClick={() => setShowConfirmation(false)}
                className="flex-1 px-4 py-2 bg-dark-600 text-dark-300 rounded-lg hover:bg-dark-500 transition-colors"
              >
                Cancelar
              </button>
              <button
                onClick={confirmUpload}
                disabled={uploading}
                className="flex-1 px-4 py-2 bg-primary-500 text-white rounded-lg hover:bg-primary-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {uploading ? 'Enviando...' : 'Confirmar e Enviar'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default UploadCSV;
